import React from "react";
import "./Vision.css";
import Division from "../Division/Division.jsx"

function Vision(){
    const name="Vision";
    return (
        <div>
           <Division name={name} />
          
            <div className="vision-text">
            <p className="content">Our vision is to make Tinkering Lab the fountainhead of plethora of technological advancements. With a zeal to work on cutting-edge technologies, we aim to provide world-class services to build the most jaw-dropping projects. To move forward on this path, we have all the different machines and equipments which need to be used. This motivates us towards our end goal and we use this self motivation as a weapon to get further in life and convert our dreams into reality. Our end goal is to make Tinkering Lab a source of pride for the nation.</p>
             <p className="content">
            With an establishment of Tinkering Lab, the Indian Institute of Technology Roorkee is poised to join a revolution that aims at converting young             minds into thinkers and innovators. The mission of Tinkering Lab is not only to strengthen the bond between industry experts and youth but also to             strive towards overall development of students, the institute, society, and the country. This can be achieved by creating an ambience which             fosters research and innovation that Tinkering Lab provides. The lab is a way to encourage a problem-solving attitude and to enkindle the             brightest minds of this country.
            </p>
            <p className="content">
            In the context of education, the Tinkering Lab refers to space where the youngsters can experiment with, learn, develop and conceptualize             different scientific ideas. It is a lot similar to DIY(Do-it-yourself) method where the students are encouraged to apply the knowledge that they             have gained in the classroom.</p>
        
        </div>
        </div>
    );
}

export default Vision;